import Button from "../Button/Button"
import * as React from "react"
import { Link } from "gatsby"
import { isNullOrUndefined } from "../../constants/globals"

export const ArtworkInfo = ({ artwork, contact }) => {

    if (isNullOrUndefined(artwork)) {
        return <></>
    }
    
    const {
        title, year, color, medium, height, width,
    } = artwork

    function getTitle() {
        const TitleTag = `h${contact ? "2" : "1"}`
        return <TitleTag>
            {title}
            <span style={{ color: "var(--primary)" }}>.</span>
        </TitleTag>
    }

    function getType() {
        const TypeTag = `h${contact ? "3" : "2"}`
        return <TypeTag>
            {color.map((c, i) => {
                return c.name + (i < color.length - 1 ? " and " : "")
            })} on {medium.name}
            , {year}
        </TypeTag>
    }

    function getDimensions() {
        return <>
            {height} x {width} cm /&nbsp;
            {Math.round(height / 2.54)} x {Math.round(width / 2.54)} in
        </>
    }

    function getPricing() {

        function isSold() {
            return artwork.isSold() && (<Button
                text="This work is already sold"
            />)
        }

        function contactUs() {
            if (!contact && !artwork.isSold()) {
                return <Button
                    className="btn"
                    text="Contact Us"
                    as={Link}
                    to={"/contact?type=artwork&slug=" + artwork.slug}
                />
            } else {
                return <></>
            }
        }

        return <>
            <h3>
                {artwork.getPrice()} â‚¬
                <span style={{ color: "var(--primary)" }}>.</span>
            </h3>
            <div>
                {isSold()}
                {contactUs()}
            </div>
        </>
    }

    return <>
        <div className={"artwork-title"}>
            {getTitle()}
        </div>
        <div className={"artwork-type"}>
            {getType()}
        </div>
        <div className={"artwork-size artwork-details"}>{getDimensions()}
        </div>
        <div className={"artwork-classification artwork-details"}>This is a unique work.</div>
        <div className={"artwork-price"}>
            <div className="banner__btns">
                {getPricing()}
            </div>
        </div>
        <div className={"artwork-description artwork-details"}>
            This work includes a certificate of authenticity.
        </div>
    </>
}