import * as React from "react"
import Layout from "../components/Layout"
import Contact from "../components/Contact/Contact"
import Seo from "../components/SEO"
import { getQueryParameter } from "../constants/globals"
import { Artist } from "../models/artist"
import BannerModule from "../components/BannerModule/BannerModule"
import { getImage } from "gatsby-plugin-image"
import { ArtworkInfo } from "../components/Artwork/ArtworkInfo"

const contact = ({ pageContext: { artist: cfArtist } }) => {
    const artist = Artist.registerFromContentful(cfArtist)
    let contactType = getQueryParameter("type")
    let contactSlug = getQueryParameter("slug")
    let artwork = null
    let bgImage = null
    if (contactType === "artwork") {
        artwork = artist.artworkBySlug(contactSlug)
        bgImage = getImage(artwork.gatsbyImage)
    }
    return (
        <>
            <Seo title="Contact Us" artist={artist} />
            <Layout>
                <BannerModule
                    title="Contact Us"
                    subTitle={<ArtworkInfo artwork={artwork} contact />}
                    bgImage={bgImage}
                >
                    <Contact />
                </BannerModule>
            </Layout>
        </>
    )
}

export default contact
